









































import { Component, Prop, Watch, Vue } from "vue-property-decorator";
import {
  dateFormatter,
  longDateFormatter,
  tickAmount
} from "@/assets/ts/common";
import { XAxis } from "@/assets/ts/interfaces";
import axios from "axios";
import {
  Swiper as SwiperClass,
  Mousewheel,
  Pagination,
  Navigation
} from "swiper";
import getAwesomeSwiper from "vue-awesome-swiper/dist/exporter";
import "swiper/swiper-bundle.css";
import { GetLocalizedText } from "@/assets/ts/localize";

SwiperClass.use([Mousewheel, Pagination, Navigation]);
Vue.use(getAwesomeSwiper(SwiperClass));
const { Swiper, SwiperSlide } = getAwesomeSwiper(SwiperClass);

Vue.component("swiper", Swiper);
Vue.component("swiper-slide", SwiperSlide);

const ranges = [7, 30, 365];
const countTypes = ["sub", "view"];

@Component
export default class ChartSwiper extends Vue {
  @Prop() memberData!: {
    name: string;
    CSSname: string;
  };

  @Watch("memberData", { immediate: true, deep: true })
  async initializeData() {
    this.$data.loading = true;
    for (let i = 0; i < countTypes.length; i++) {
      const countType = countTypes[i];
      for (let j = 0; j < ranges.length; j++) {
        const range = ranges[j];
        await axios({
          method: "POST",
          url: "https://hologrowth-server.kyrie5701.com/get-member-data",
          headers: { "content-type": "application/json" },
          data: {
            range: range,
            talent: this.memberData.name,
            countType: countType
          }
        })
          .then(res => {
            const size = Object.values(res.data).length;
            this.$data.allSeries[`${countType}-${range}`] = [
              {
                name: GetLocalizedText(countType),
                data: Object.values(res.data)
              }
            ];
            this.$data.xaxis[`${countType}-${range}`] = {
              categories: Object.keys(res.data),
              labels: {
                formatter: size >= 365 ? longDateFormatter : dateFormatter
              },
              tickAmount: tickAmount
            };
          })
          .catch(e => console.error(e));
      }
    }
    this.$data.loading = false;
  }

  data() {
    return {
      loading: false,
      ranges: ranges,
      countTypes: countTypes,
      allSeries: {
        "sub-7": [],
        "sub-30": [],
        "sub-365": [],
        "view-7": [],
        "view-30": [],
        "view-365": []
      },
      xaxis: {
        "sub-7": {} as XAxis,
        "sub-30": {} as XAxis,
        "sub-365": {} as XAxis,
        "view-7": {} as XAxis,
        "view-30": {} as XAxis,
        "view-365": {} as XAxis
      },
      swiperOptionh: {
        spaceBetween: 50,
        pagination: {
          el: ".swiper-pagination-h",
          clickable: true,
          renderBullet(index: number, className: string) {
            return `<span id="${index}h" class="${className} swiper-pagination-bullet-custom"></span>`;
          }
        }
      },
      swiperOptionv: {
        direction: "vertical",
        spaceBetween: 50,
        pagination: {
          el: ".swiper-pagination-v",
          clickable: true,
          renderBullet(index: number, className: string) {
            return `<span id="${index}v" class="${className} swiper-pagination-bullet-custom"></span>`;
          }
        }
      }
    };
  }
}
